@use 'base/variables' as v;
@use 'base/mixins' as m;

.tiendas {
    display: flex;
    flex-direction: column;
    justify-content: center;


    &__heading,
    &__texto {
        text-align: center;
    }
    &__heading {
        color: v.$secundario;
        margin: 2rem 0 0 0;
    }
    &__grid {
        @include m.grid(2, 1rem);
    }
    &__texto {
        margin-bottom: 3rem;
    }
    &__enlace {
        transition: transform 500ms ease;
        &:hover {
            transform: scale(1.2);
        }
    }
}